@import 'assets/scss/utils/mixins';

.w-600px {
	max-width: 600px;
}

.h-100 {
	height: 100% !important;
}

.profile-container {
	padding-bottom: 89px !important;

	@include media-breakpoint-up(lg) {
		padding-bottom: 70px !important;
	}
}

.radio-btn-item {
	border: 1px solid var(--general-3);
	height: 40px;
	border-radius: 4px;
	text-decoration: none;
	cursor: pointer;
}

a {
	mat-icon {
		display: inline !important;
		vertical-align: middle !important;
		margin-inline-start: 4px;
	}
}

.mw-550px {
	max-width: 550px;
}

findqo-button {
	height: 40px;
}

.w-125px {
	width: 125px;
}

.container-fluid {
	z-index: var(--z-index-place-ad-action-btn);
}

.feedback-chip {
	border-radius: 4px !important;
	background-color: white !important;
	border: 1px solid var(--general-2);
	min-height: 40px;
}

.feedback-chip-selected {
	border-radius: 4px !important;
	border: 1px solid #ff5ab8;
	color: #ff5ab8;
	min-height: 40px;
	background-color: #feebf6 !important;
}
